import styles from "./App.module.css";
import { observer, useLocalObservable } from "mobx-react-lite";
import { AuthStore } from "./AuthStore";
import { useEffect, useState } from "react";

type Signature = {
  dataHash: string;
  signature: string;
  signatureHex: string;
  signatureParts: {
    high: string;
    low: string;
  };
};

const App = observer(() => {
  const auth = useLocalObservable(() => new AuthStore());

  const [message, setMessage] = useState("");

  const [signedData1, setSignedData1] = useState<Signature | null>(null);
  const [signedData2, setSignedData2] = useState<Signature | null>(null);
  const [signedData3, setSignedData3] = useState<Signature | null>(null);

  useEffect(() => {
    auth.init();
  }, [auth]);

  if (!auth.initialized) {
    return (
      <div className={styles.main}>
        <span>Loading...</span>
      </div>
    );
  }

  if (!auth.loggedIn) {
    return (
      <div className={styles.main}>
        <button
          onClick={() => {
            auth.login();
          }}
        >
          Connect wallet
        </button>
      </div>
    );
  }

  return (
    <div className={styles.main}>
      <button
        onClick={() => {
          auth.logout();
        }}
      >
        Disconnect
      </button>

      <div className={styles.account_info}>
        <span>Address: {auth.account?.address}</span>
        <span>NetworkId: {auth.account?.networkId}</span>
        <span>Public key: {auth.account?.publicKey}</span>
        <span>Wallet type: {auth.account?.walletType}</span>
      </div>

      {!signedData1 || !signedData2 || !signedData3 ? (
        <>
          <textarea
            className={styles.text_area}
            value={message}
            onChange={(val) => {
              setMessage(val.target.value);
            }}
          ></textarea>

          <button
            onClick={async () => {
              const signature1 = await auth.signMessage(message, false);
              const signature2 = await auth.signMessage(message, true);
              const signature3 = await auth.signMessage(message, 777);

              setSignedData1(signature1);
              setSignedData2(signature2);
              setSignedData3(signature3);
            }}
          >
            Sign 3 messages
          </button>
        </>
      ) : (
        <>
          <div className={styles.account_info}>
            <span>Message:</span>
            <span>{message}</span>
            <br />
            <span>Signature:</span>
            <span>{signedData1.signature}</span>
            <br />
            <span>Signature (withSignatureId: true):</span>
            <span>{signedData2.signature}</span>
            <br />
            <span>Signature (withSignatureId: 777):</span>
            <span>{signedData3.signature}</span>
          </div>
        </>
      )}
    </div>
  );
});

export default App;
